import { parseApiError } from 'pages/costModels/createCostModelWizard/parseError';
import { selectPagination } from 'store/djangoUtils/pagination';
import { selectQuery } from 'store/djangoUtils/query';
import { stateKey } from './reducer';
export const costModelsState = (state) => state[stateKey];
export const costModels = (state) => {
    const cms = costModelsState(state).costModels;
    if (cms) {
        return cms.data;
    }
    return [];
};
export const isDialogOpen = (state) => {
    const dialogs = costModelsState(state).isDialogOpen;
    return (type) => {
        switch (type) {
            case 'markup':
                const { deleteMarkup, updateMarkup } = dialogs;
                return { deleteMarkup, updateMarkup };
            case 'costmodel':
                const { deleteCostModel, updateCostModel } = dialogs;
                return { deleteCostModel, updateCostModel };
            case 'sources':
                const { deleteSource, addSource } = dialogs;
                return { deleteSource, addSource };
            case 'rate':
                const { addRate, updateRate, deleteRate } = dialogs;
                return { addRate, updateRate, deleteRate };
            default:
                return costModelsState(state).isDialogOpen;
        }
    };
};
export const status = (state) => costModelsState(state).status;
export const error = (state) => costModelsState(state).error;
export const currentFilterValue = (state) => costModelsState(state).currentFilterValue;
export const currentFilterType = (state) => costModelsState(state).currentFilterType;
export const query = selectQuery((state) => costModelsState(state).costModels, ['ordering', 'name', 'source_type', 'description', 'offset', 'limit']);
export const pagination = selectPagination((state) => costModelsState(state).costModels);
export const updateProcessing = (state) => costModelsState(state).update.status === 1 /* inProgress */;
export const updateError = (state) => {
    const updateErr = costModelsState(state).update.error;
    if (updateErr === null) {
        return '';
    }
    return parseApiError(updateErr);
};
export const selected = (state) => {
    return costModelsState(state).update.current;
};
export const deleteProcessing = (state) => costModelsState(state).delete.status === 1 /* inProgress */;
export const deleteError = (state) => {
    const err = costModelsState(state).delete.error;
    if (err === null) {
        return '';
    }
    return parseApiError(err);
};
