import React from 'react';
export function asyncComponent(loader) {
    let LoadedComponent = null;
    class Async extends React.Component {
        constructor() {
            super(...arguments);
            this.state = {
                isLoading: !Boolean(LoadedComponent),
            };
        }
        componentDidMount() {
            if (!this.state.isLoading) {
                return;
            }
            loader().then(Comp => {
                LoadedComponent = Boolean(Comp.default)
                    ? Comp.default
                    : Comp;
                this.setState({ isLoading: false });
            });
        }
        render() {
            const { isLoading } = this.state;
            return isLoading ? null : React.createElement(LoadedComponent, Object.assign({}, this.props));
        }
    }
    return Async;
}
