import { stateKey } from './uiReducer';
export const selectUIState = (state) => state[stateKey];
export const selectIsExportModalOpen = (state) => selectUIState(state).isExportModalOpen;
export const selectIsProvidersModalOpen = (state) => selectUIState(state).isProvidersModalOpen;
export const selectIsSidebarOpen = (state) => selectUIState(state).isSidebarOpen;
