import { getType } from 'typesafe-actions';
import { setWidgetTab } from './ocpUsageDashboardActions';
import { costSummaryWidget, cpuWidget, memoryWidget, volumeWidget, } from './ocpUsageDashboardWidgets';
export const defaultState = {
    currentWidgets: [
        costSummaryWidget.id,
        cpuWidget.id,
        memoryWidget.id,
        volumeWidget.id,
    ],
    widgets: {
        [costSummaryWidget.id]: costSummaryWidget,
        [cpuWidget.id]: cpuWidget,
        [memoryWidget.id]: memoryWidget,
        [volumeWidget.id]: volumeWidget,
    },
};
export function ocpUsageDashboardReducer(state = defaultState, action) {
    switch (action.type) {
        case getType(setWidgetTab):
            return Object.assign(Object.assign({}, state), { widgets: Object.assign(Object.assign({}, state.widgets), { [action.payload.id]: Object.assign(Object.assign({}, state.widgets[action.payload.id]), { currentTab: action.payload.tab }) }) });
        default:
            return state;
    }
}
